:root {
  --color-principal: #00e1ae;
  --color-boton: #006cfc;
  --color-borde-boton: #001997;
  --font-main: "Odibee Sans", cursive;
}

* {
  box-sizing: border-box;
}

body, html {
  font-family: var(--font-main);
  background-image: url("fondo.fdbfaf9b.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

#root {
  flex-direction: column;
  align-items: center;
  height: 100%;
  display: flex;
}

.main-container {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 400px;
  height: 100%;
  margin: 0 auto;
  padding: 60px 20px 0;
  display: flex;
}

.btn-principal {
  background-color: var(--color-boton);
  border: 10px solid var(--color-borde-boton);
  color: #fff;
  width: 100%;
  height: 87px;
  font-family: var(--font-main);
  cursor: pointer;
  border-radius: 10px;
  font-size: 45px;
  transition: all .1s;
}

.btn-principal:active {
  transform: scale(.95);
}

.hands-container {
  justify-content: center;
  align-items: flex-end;
  gap: 20px;
  width: 100%;
  display: flex;
  position: relative;
  bottom: -20px;
}

my-jugada {
  width: 25%;
  min-width: 80px;
  max-width: 120px;
}

.play__hands {
  margin-top: auto;
}

.hand-selected {
  transition: transform .3s;
  transform: translateY(-50px);
}

.hand-inactive {
  opacity: .5;
  filter: grayscale();
}

.results-screen {
  z-index: 10;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
  position: absolute;
  inset: 0;
}

.star-container {
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 250px;
  margin-bottom: 20px;
  display: flex;
  position: relative;
}

.star-img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.result-label {
  color: #fff;
  letter-spacing: .05em;
  text-shadow: 2px 2px #0003;
  font-size: 55px;
  position: absolute;
}

.countdown-wrapper {
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 200px;
  display: flex;
  position: relative;
}

.timer-svg {
  width: 100%;
  height: 100%;
  position: absolute;
  transform: rotate(-90deg);
}

.timer-circle-bg {
  fill: none;
  stroke: #0000001a;
  stroke-width: 8px;
}

.timer-circle-path {
  fill: none;
  stroke: #000;
  stroke-width: 8px;
  stroke-linecap: round;
  stroke-dasharray: 283;
  stroke-dashoffset: 0;
  animation: 3s linear forwards countdown-animation;
}

@keyframes countdown-animation {
  from {
    stroke-dashoffset: 0;
  }

  to {
    stroke-dashoffset: 283px;
  }
}

.number {
  font-size: 80px;
  font-weight: bold;
}

.hand-img {
  width: 100%;
  height: auto;
  display: block;
}

:host {
  width: 25vw;
  max-width: 100px;
  display: inline-block;
}

@media (width >= 769px) {
  :host {
    width: 15vw;
    max-width: 150px;
  }
}
